<template>
  <div class="container">
    <el-row type="flex" class="row-bg" justify="center">
      <span class="title">请选择房型</span>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-date-picker v-model="value6" type="daterange" range-separator="至" start-placeholder="入住日期" end-placeholder="离店日期">
      </el-date-picker>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <span>入住天数：</span>
      <el-input-number v-model="days" @change="handleDaysChange" :min="1" label="入住天数"></el-input-number>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="16">
      </el-col>
      <el-col :span="8">
        <router-link :to="{name: 'home'}">
          <el-button type="primary" class="btn_large">返回</el-button>
        </router-link>
        <router-link :to="{name: 'selet-room'}">
          <el-button type="primary" class="btn_large">去选房型</el-button>
        </router-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      days: 1
    };
  },
  methods: {
    handleDaysChange(days) {
      console.log(days);
    }
  }
};
</script>

<style scoped>
.title {
  font-size: 32px;
}

.el-row {
  margin-top: 20px;
}

.el-button {
  margin-left: 20px;
}

.btn_large {
  margin-top: 20px;
  font-size: 32px;
}
</style>



